<template>
  <div id="Menu">
    <div class="ball_back" id="ball_back" @mousemove="rotate_menu"></div>
    <div class="ball" id="ball" @click="click_arrow"></div>
    <img
      src="../../../static/menu/arrow_left_black.png"
      class="menu_arrow"
      id="menu_arrow"
      @click="click_arrow"
    />

    <div
      class="menu_clock_arrow_box"
      id="menu_clock_arrow_box"
      @mousemove="rotate_menu"
    >
      <img
        src="../../../static/menu/arrow_right_white.png"
        class="menu_clock_arrow"
        id="menu_clock_arrow"
      />
    </div>

    <div
      class="menu_clock_circle"
      id="menu_clock_circle"
      @mousemove="rotate_menu"
    >
      <div class="menu_item menu_item0" id="menu_item0" @click="to_map">
        <div class="clock_dot clock_dot0" id="clock_dot0"></div>
        <div class="menu_num menu_num0" id="menu_num0">00</div>
        <div class="menu_text_top menu_text_top0" id="menu_text_top0">
          {{ global.lang_pack.signcheck }}
        </div>
        <div class="menu_bottom menu_bottom0" id="menu_bottom0">
          {{ global.lang_pack.signcheck_map }}
        </div>
      </div>

      <div class="menu_item menu_item1" id="menu_item1" @click="to_about">
        <div class="clock_dot clock_dot1" id="clock_dot1"></div>
        <div class="menu_num menu_num1" id="menu_num1">01</div>
        <div class="menu_text_top menu_text_top1" id="menu_text_top1">
          {{ global.lang_pack.about }}
        </div>
        <div class="menu_bottom menu_bottom1" id="menu_bottom1">
          {{ global.lang_pack.about_us }}
        </div>
      </div>

      <div class="menu_item menu_item2" id="menu_item2" @click="to_works">
        <div class="clock_dot clock_dot2" id="clock_dot2"></div>
        <div class="menu_num menu_num2" id="menu_num2">02</div>
        <div class="menu_text_top menu_text_top2" id="menu_text_top2">
          {{ global.lang_pack.works }}
        </div>
        <div class="menu_bottom menu_bottom2" id="menu_bottom2">
          {{ global.lang_pack.our_works }}
        </div>
      </div>

      <div class="menu_item menu_item3" id="menu_item3" @click="to_past">
        <div class="clock_dot clock_dot3" id="clock_dot3"></div>
        <div class="menu_num menu_num3" id="menu_num3">03</div>
        <div class="menu_text_top menu_text_top3" id="menu_text_top3">
          {{ global.lang_pack.past }}
        </div>
        <div class="menu_bottom menu_bottom3" id="menu_bottom3">
          {{ global.lang_pack.our_past }}
        </div>
      </div>

      <div class="menu_item menu_item4" id="menu_item4" @click="to_team">
        <div class="clock_dot clock_dot4" id="clock_dot4"></div>
        <div class="menu_num menu_num4" id="menu_num4">04</div>
        <div class="menu_text_top menu_text_top4" id="menu_text_top4">
          {{ global.lang_pack.team }}
        </div>
        <div class="menu_bottom menu_bottom4" id="menu_bottom4">
          {{ global.lang_pack.our_team }}
        </div>
      </div>

      <div class="menu_item menu_item5" id="menu_item5" @click="to_contact">
        <div class="clock_dot clock_dot5" id="clock_dot5"></div>
        <div class="menu_num menu_num5" id="menu_num5">05</div>
        <div class="menu_text_top menu_text_top5" id="menu_text_top5">
          {{ global.lang_pack.contact }}
        </div>
        <div class="menu_bottom menu_bottom5" id="menu_bottom5">
          {{ global.lang_pack.contact_us }}
        </div>
      </div>
    </div>
    <Logo></Logo>
  </div>
</template>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
:root {
  --circle_diameters: 0rem;
}

div {
  font-family: MyriadVariableConcept-Roman;
}

div.ball {
  position: fixed;
  z-index: 100;
  right: calc(-50rem);
  top: calc(50% - 50rem);
  width: calc(100rem);
  height: calc(100rem);
  background-color: #efcd24;
  border-radius: 50%;
  pointer-events: fill;
  cursor: pointer;
}

div.ball_back {
  position: fixed;
  z-index: 99;
  right: calc(-50rem);
  top: calc(50% - 50rem);
  width: calc(100rem);
  height: calc(100rem);
  background-color: #efcd24;
  border-radius: 50%;
}

img.menu_arrow {
  cursor: pointer;

  width: calc(30rem);
  position: fixed;
  z-index: 100;
  right: calc(10rem);
  top: calc(50% - 11.35rem);
  pointer-events: none;
}

div.menu_clock_arrow_box {
  visibility: hidden;
  position: fixed;
  z-index: 99;
  transform-origin: center;
  left: calc(960rem - var(--circle_diameters));
  top: calc(50% - var(--circle_diameters) / 2);
  width: calc(var(--circle_diameters));
  height: calc(var(--circle_diameters));
}

img.menu_clock_arrow {
  position: relative;
  top: 50%;
  left: calc(var(--circle_diameters) - 170rem);
  transform: translate(0%, -50%);
  width: 150rem;
}

div.menu_clock_circle {
  position: fixed;
  z-index: 99;
  visibility: hidden;
  transform-origin: center;
  left: calc(960rem - var(--circle_diameters));
  top: calc(50% - var(--circle_diameters) / 2);
  border-radius: 50%;
  border: calc(2rem) solid #ffffff;
  box-sizing: content-box;
  width: calc(var(--circle_diameters));
  height: calc(var(--circle_diameters));
}

div.menu_item {
  display: grid;
  cursor: pointer;
  padding-top: calc(37.5rem);
  width: fit-content;
  transform-origin: left 50%;
  height: auto;
  background-color: transparent;
  position: fixed;
}

div.menu_item0 {
  transform: translate(0, -50%) rotate(0deg);
  left: calc(
    var(--circle_diameters) / 2 + var(--circle_diameters) / 2 - 7.5rem
  );
  top: calc(var(--circle_diameters) / 2);
}

div.menu_item1 {
  transform: translate(0, -50%) rotate(22.5deg);
  left: calc(
    var(--circle_diameters) / 2 + var(--circle_diameters) / 2 * 0.924 - 7.5rem *
      0.924
  );
  top: calc(
    var(--circle_diameters) / 2 + var(--circle_diameters) / 2 * 0.383 - 7.5rem *
      0.383
  );
}

div.menu_item2 {
  transform: translate(0, -50%) rotate(45deg);
  left: calc(
    var(--circle_diameters) / 2 + var(--circle_diameters) / 2 * 0.707 - 7.5rem *
      0.707
  );
  top: calc(
    var(--circle_diameters) / 2 + var(--circle_diameters) / 2 * 0.707 - 7.5rem *
      0.707
  );
}

div.menu_item3 {
  transform: translate(0, -50%) rotate(67.5deg);
  left: calc(
    var(--circle_diameters) / 2 + var(--circle_diameters) / 2 * 0.383 - 7.5rem *
      0.383
  );
  top: calc(
    var(--circle_diameters) / 2 + var(--circle_diameters) / 2 * 0.924 - 7.5rem *
      0.924
  );
}

div.menu_item4 {
  transform: translate(0, -50%) rotate(90deg);
  left: calc(var(--circle_diameters) / 2);
  top: calc(var(--circle_diameters) / 2 + var(--circle_diameters) / 2 - 7.5rem);
}

div.menu_item5 {
  transform: translate(0, -50%) rotate(112.5deg);
  left: calc(
    var(--circle_diameters) / 2 - var(--circle_diameters) / 2 * 0.383 + 7.5rem *
      0.383
  );
  top: calc(
    var(--circle_diameters) / 2 + var(--circle_diameters) / 2 * 0.924 - 7.5rem *
      0.924
  );
}

div.clock_dot {
  width: 15rem;
  height: 15rem;
  border-radius: 50%;
  align-self: center;
  margin-right: 30rem;
  grid-row: 1;
  grid-column: 1;
}

div.clock_dot0 {
  background-color: #ffffff;
}

div.clock_dot1 {
  background-color: #ffffff;
}

div.clock_dot2 {
  background-color: #ffffff;
}

div.clock_dot3 {
  background-color: #ffffff;
}

div.clock_dot4 {
  background-color: #ffffff;
}

div.clock_dot5 {
  background-color: #ffffff;
}

div.menu_num {
  font-weight: bolder;
  letter-spacing: 5rem;
  font-size: 80rem;
  align-self: center;
  color: #37383d;
  padding-right: 50rem;
  grid-row: 1;
  grid-column: 2;
}

div.menu_text_top {
  font-weight: bolder;
  letter-spacing: 5rem;
  text-transform: uppercase;
  font-size: 80rem;
  align-self: center;
  color: #37383d;
  grid-row: 1;
  grid-column: 3;
}

div.menu_bottom {
  font-weight: normal;
  letter-spacing: 2rem;
  font-size: 35rem;
  top: -10rem;
  color: #37383d;
  grid-row: 2;
  grid-column: 3;
}
</style>


<script>
import global from "../../pages/global";
import anime from "../../../modules/anime";
import Logo from "../../components/logo/Logo";

let menu_roll_down = false;
let menu_roll_up = false;
let menu_roll_event;
let menu_change;

export default {
  name: "Menu",
  data() {
    return {
      // mark is the menu shown
      is_show_menu: false,
      // import global data
      global: global,
      // mark which menu item is focused
      menu_focus: 0,
      // mark curent rotate
      menu_rotate: 0,
    };
  },
  components: {
    Logo,
  },
  beforeMount() {},
  mounted() {
    global.set_lang_pack();
    global.change_root_size();
    //initial the clock circle size
    this.change_menu_clock_circle();
    // listen the size of window to change the base size
    window.onresize = () => {
      return (() => {
        global.change_root_size();
      })();
    };
    global.change_root_size();
  },
  watch: {
    "global.width": function (val, oldVal) {
      this.refresh_root_size();
    },
  },
  destroyed() {},
  methods: {
    // refresh the root size
    refresh_root_size() {
      // if width is less than this value, don't change the basic size
      let static_piexl = 0;
      if (global.width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize =
        document.documentElement.clientWidth / 1920 + "px";
      if (this.is_show_menu) {
        this.change_menu_clock_circle();
        this.show_menu();
      }
    },
    // show_menu
    click_arrow() {
      if (this.is_show_menu) {
        this.is_show_menu = false;
        this.hide_menu();
        this.hide_menu_clock();
        this.menu_rotate = 0;
        this.clear_menu_focus();
        clearInterval(menu_roll_event);
        clearInterval(menu_change);
      } else {
        this.show_menu();
        this.show_menu_clock();
        this.create_menu_change();
        let that = this;
        setTimeout(function () {
          that.is_show_menu = true;
        }, 1167);
      }
    },
    show_menu() {
      // pointer-events: fill;
      document.getElementById("ball").style.pointerEvents = "none";
      new anime({
        targets: "div.ball_back",
        scale:
          Math.sqrt(
            Math.pow(document.documentElement.clientWidth, 2) +
              Math.pow(document.documentElement.clientHeight, 2)
          ) /
          ((document.documentElement.clientWidth / 1920) * 50),
        easing: "easeInOutExpo",
        duration: 1000,
      });

      new anime({
        targets: "div.ball",
        backgroundColor: "#ffffff",
        easing: "linear",
        duration: 500,
      });

      new anime({
        targets: "img.menu_arrow",
        rotate: 180,
        easing: "linear",
        duration: 500,
      });
    },

    hide_menu() {
      new anime({
        delay: 500,
        targets: "div.ball_back",
        scale: 1,
        easing: "easeInOutExpo",
        duration: 1000,
      });

      new anime({
        delay: 500,
        targets: "div.ball",
        backgroundColor: "#efcd24",
        easing: "linear",
        duration: 500,
      });

      new anime({
        delay: 500,
        targets: "img.menu_arrow",
        rotate: 0,
        easing: "linear",
        duration: 500,
        complete: function (anim) {
          document.getElementById("ball").style.pointerEvents = "fill";
        },
      });
    },

    show_menu_clock() {
      let that = this;
      new anime({
        delay: 500,
        targets: "div.menu_clock_circle",
        rotate: [60, 0],
        easing: "linear",
        duration: 667,
        change: function (anim) {
          document.getElementById("menu_clock_circle").style.visibility =
            "visible";
        },
        complete: function (anim) {
          document.getElementById("ball").style.pointerEvents = "fill";
          that.init_menu_focus();
        },
      });

      new anime({
        delay: 500,
        targets: "div.menu_clock_arrow_box",
        rotate: [60, 0],
        easing: "linear",
        duration: 667,
        change: function (anim) {
          document.getElementById("menu_clock_arrow_box").style.visibility =
            "visible";
        },
      });
    },

    change_menu_clock_circle() {
      let radius =
        (Math.max(global.width / 2, global.height) / global.width) * 1920 * 1.5;
      document.documentElement.style.setProperty(
        "--circle_diameters",
        radius + "rem"
      );
      // document.documentElement.style.setProperty(
      //   "--circle_diameters",
      //   "500rem"
      // );
    },
    hide_menu_clock() {
      let that = this;
      document.getElementById("ball").style.pointerEvents = "none";
      new anime({
        targets: "div.menu_clock_circle",
        rotate: [this.menu_rotate, 90],
        easing: "linear",
        duration: 1000,
        complete: function (anim) {
          document.getElementById("menu_clock_circle").style.visibility =
            "hidden";
          that.clear_menu_focus();
        },
      });

      new anime({
        targets: "div.menu_clock_arrow_box",
        rotate: [0, 90],
        easing: "linear",
        duration: 1000,
        complete: function (anim) {
          document.getElementById("menu_clock_arrow_box").style.visibility =
            "hidden";
        },
      });
    },
    rotate_menu(event) {
      if (this.is_show_menu === false) return;
      let cury = event.y;
      let bound = 100;
      let rem =
        (document.documentElement.clientWidth || document.body.clientWidth) /
        1920;
      let height =
        document.documentElement.clientHeight || document.body.clientHeight;
      // if mouse in select menu don't change menu rotate
      let bound_up = height / 2 + rem * bound;
      let bound_down = height / 2 - rem * bound;

      // if mouse in top part
      if (cury <= bound_down) {
        if (menu_roll_up === true) {
          menu_roll_up = false;
        }
        if (menu_roll_down === true) {
          return;
        }
        menu_roll_down = true;
        clearInterval(menu_roll_event);

        menu_roll_event = setInterval(() => {
          // console.log(this.menu_rotate)
          if (this.menu_rotate + 22.5 > 0) {
            this.menu_rotate = 0;
            // return
          } else {
            this.menu_rotate += 22.5;
            new anime({
              targets: "div.menu_clock_circle",
              rotate: [this.menu_rotate - 22.5, this.menu_rotate],
              easing: "linear",
              duration: 500,
            });
          }
          // document.getElementById("menu_clock_circle").style.transform =
          //   "rotate(" + this.menu_rotate + "deg)";
        }, 500);
      }

      // if mouse in down part
      if (cury >= bound_up) {
        if (menu_roll_down == true) {
          menu_roll_down = false;
        }
        if (menu_roll_up == true) {
          return;
        }
        menu_roll_up = true;
        clearInterval(menu_roll_event);

        menu_roll_event = setInterval(() => {
          // console.log(this.menu_rotate)
          if (this.menu_rotate - 22.5 < -112.5) {
            this.menu_rotate = -112.5;
            // return
          } else {
            this.menu_rotate -= 22.5;
            new anime({
              targets: "div.menu_clock_circle",
              rotate: [this.menu_rotate + 22.5, this.menu_rotate],
              easing: "linear",
              duration: 500,
            });
          }
        }, 500);
      }

      if (cury > bound_down && cury < bound_up) {
        menu_roll_down = false;
        menu_roll_up = false;
        clearInterval(menu_roll_event);
      }
    },

    create_menu_change() {
      let last_menu_focus = 0;
      menu_change = setInterval(() => {
        switch (this.menu_rotate) {
          case 0:
            this.menu_focus = 0;
            break;
          case -22.5:
            this.menu_focus = 1;
            break;
          case -45:
            this.menu_focus = 2;
            break;
          case -67.5:
            this.menu_focus = 3;
            break;
          case -90:
            this.menu_focus = 4;
            break;
          case -112.5:
            this.menu_focus = 5;
            break;
        }
        let roll_state = this.menu_focus - last_menu_focus;
        if (roll_state !== 0) {
          switch (this.menu_focus) {
            case 0:
              if (roll_state < 0) {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus + 1);
              } else {
              }
              break;
            case 1:
              if (roll_state < 0) {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus + 1);
              } else {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus - 1);
              }
              break;
            case 2:
              if (roll_state < 0) {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus + 1);
              } else {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus - 1);
              }
              break;
            case 3:
              if (roll_state < 0) {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus + 1);
              } else {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus - 1);
              }
              break;
            case 4:
              if (roll_state < 0) {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus + 1);
              } else {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus - 1);
              }
              break;
            case 5:
              if (roll_state < 0) {
              } else {
                this.get_focus(this.menu_focus);
                this.lose_focus(this.menu_focus - 1);
              }
              break;
          }
        }
        last_menu_focus = this.menu_focus;
      }, 100);
    },

    lose_focus(n) {
      new anime({
        targets: ["div.menu_text_top" + n, "div.menu_bottom" + n],
        opacity: ["100%", "0%"],
        easing: "linear",
        duration: 500,
      });

      new anime({
        targets: "div.menu_num" + n,
        opacity: ["100%", "50%"],
        easing: "linear",
        duration: 500,
      });

      new anime({
        targets: "div.clock_dot" + n,
        backgroundColor: "#f8e791",
        easing: "linear",
        scale: 0.7,
        duration: 500,
      });
    },

    get_focus(n) {
      this.menu_focus = n;
      new anime({
        targets: ["div.menu_text_top" + n, "div.menu_bottom" + n],
        opacity: ["0%", "100%"],
        easing: "linear",
        duration: 500,
      });

      new anime({
        targets: "div.menu_num" + n,
        opacity: ["50%", "100%"],
        easing: "linear",
        duration: 500,
      });

      new anime({
        targets: "div.clock_dot" + n,
        backgroundColor: "#ffffff",
        easing: "linear",
        scale: 1,
        duration: 500,
      });
    },
    init_menu_focus() {
      this.lose_focus(1);
      this.lose_focus(2);
      this.lose_focus(3);
      this.lose_focus(4);
      this.lose_focus(5);
    },
    clear_menu_focus() {
      if (this.menu_focus !== 0) this.get_focus(0);
      if (this.menu_focus !== 1) this.get_focus(1);
      if (this.menu_focus !== 2) this.get_focus(2);
      if (this.menu_focus !== 3) this.get_focus(3);
      if (this.menu_focus !== 4) this.get_focus(4);
      if (this.menu_focus !== 5) this.get_focus(5);
    },
    change_logo_color(signch_ck, e, dur) {
      Logo.methods.change_color(signch_ck, e, dur);
    },
    to_map() {
      this.click_arrow();
      let that = this;
      setTimeout(function () {
        that.$router.push("/Map");
      }, 1500);
    },
    to_about() {
      this.click_arrow();
      let that = this;
      setTimeout(function () {
        that.$router.push("/About");
      }, 1500);
    },
    to_works() {
      this.click_arrow();
      let that = this;
      setTimeout(function () {
        that.$router.push("/Works");
      }, 1500);
    },
    to_past() {
      this.to_map();
    },
    to_team() {
      this.click_arrow();
      let that = this;
      setTimeout(function () {
        that.$router.push("/Team");
      }, 1500);
    },
    to_contact() {
      this.click_arrow();
      let that = this;
      setTimeout(function () {
        that.$router.push("/Contact");
      }, 1500);
    },
  },
};
</script>



